<template>
    <div class="movie_menu">
          <el-row>
              <el-col :span="6" v-for="i in movie_menu_img.data" >
                <div class="grid-content bg-purple">
                  <img :src="i.img_url" :alt="i.id" @click="go_this($event)">
                </div>
              </el-col>
          </el-row> 
    </div>
  
  </template>
  
  <script>
  
  import {mapState} from 'vuex'
  export default {
    created(){
      // getall();
    },
    data() {
        return {
          // i:'s'
        };
      },
    computed:{
      ...mapState({
        movie_menu_img: state => state.movie_store.movie_menu_img
      })
    },
    mounted(){
      this.$store.dispatch('load_movie_menu_img')
    },
    methods:{
      go_this(){
        const currentImgId = event.currentTarget.alt;
        this.$router.push({path:'/Movie_play', name: 'Movie_play',query:{playId:currentImgId}})
      }
    }
  }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
    .movie_menu{
      padding-top: 16px;
      overflow: auto;
    }
    .el-col {
      border-radius: 4px;
    }
    .bg-purple img {
      width: 160px;
    }
  </style>
  


